@import "mixins";
@import "vars";
@import "generated/sprite-svg";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}
html,
body {
  height: 100%;
  @include rfs(30);
}
.wrapper {
  width: 100%;
  font-family: $font-primary;
  font-size: 1rem;
}
a {
  color: #fff;
  &:hover {
    text-decoration: none;
  }
}
ul li {
  list-style-type: none;
}
.icon {
  fill: #fff;
  max-width: 100vw;
}
// Header
.header {
  height: 4.25rem;
  border: solid 1px #707070;
  border-bottom: 0px;
  .navbar {
    height: 4.25rem;
    padding-top: 0;
    padding-bottom: 0;
  }
  .navbar-brand {
    padding: 0;
  }
  .navbar-collapse {
    position: absolute;
    top: 99%;
    left: 0;
    background-color: #fff;
    z-index: 2;
  }
  .main-menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: 24vw;
    min-height: calc(100vh - 4.25rem);
    padding: 2rem 3rem 2rem 4rem;
    .nav-item {
      margin-right: 1vw;
      @media only screen and (max-width: 992px) {
        margin: 0;
      }
      &.active .nav-link {
        border-color: #ff4000;
        color: #ff4000;
      }
    }
    .nav-link {
      display: flex;
      align-items: center;
      cursor: pointer;
      width: 100%;
      font-family: $font-asap;
      font-size: 0.8rem;
      font-style: normal;
      font-stretch: condensed;
      line-height: 2;
      letter-spacing: 1.7px;
      text-align: left;
      color: rgba(255, 64, 0, 0.6);
      transition: all 0.3s ease;
      @media only screen and (min-width: 480px) {
        padding: 0.25rem 0;
      }
      &:hover {
        color: #ff4000;
        border-color: #ff4000;
        .icon {
          fill: #ff4000;
        }
      }
      &.button {
        width: 155.2px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
          color: #ffffff;
        }
      }
      .icon {
        margin-right: 1vw;
      }
    }
    .dropdown-menu {
      min-width: 10rem;
      padding: 0;
      margin: 0;
      font-size: 1em;
      border: none;
      border-radius: 0;
      box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1),
        0 8px 15px 0 rgba(0, 0, 0, 0.1);
    }
    .dropdown-item {
      color: #000000;
      padding: 0.25rem 0.6em;
      &:hover {
        // color: #ff4000

        background: transparent;
        background-color: #f0fbfb;
      }
      &:active {
        // color: #ffffff;
      }
    }
  }
  .navbar-toggler {
    position: absolute;
    left: 1.6rem;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto;
    padding: 0;
    width: 1.3rem;
    border: none;
    color: #000;
    &:focus {
      outline: none !important;
    }
  }

  .navbar-toggler-icon {
    width: 2rem;
    height: 2rem;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#ff4000' stroke-width='3' stroke-linecap='butt' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
    @media only screen and (max-width: 768px) {
      margin-left: 0;
    }
  }
}
.main-logo {
  width: 2.5rem;
  height: 2.5rem;
}

// Hero
.hero {
  padding-left: $pdgleft;
  &__inner {
    position: relative;
    min-height: calc(100vh - 4.25rem);
    padding: 2.6rem 2.6rem 2.6rem 4rem;
    overflow: hidden;
    color: #ffffff;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 0.6rem;
      height: 38%;
      background-color: #fff;
    }
  }
  &__text {
    max-width: 620px;
  }
  &__title {
    font-family: $font-asap;
    font-size: 2.4rem;
    font-weight: 500;
    font-style: normal;
    font-stretch: condensed;
    line-height: 1.39;
    letter-spacing: 0.2em;
  }
  &__descr {
    margin: 1.6rem 0 1rem;
    font-size: 1rem;
    line-height: 1.33;
    letter-spacing: 0.05em;
  }
  &__more {
    display: flex;
    align-items: center;
    margin-top: 1rem;
    font-size: 0.8rem;
    font-family: $font-shareTech;
    letter-spacing: 0.1em;
    &:hover {
      color: #ccc;
      .icon {
        fill: #ccc;
      }
    }
    u {
      margin-right: 0.5rem;
    }
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center bottom;
    z-index: -1;
    background-color: #ff4000;
  }
  .arrow {
    width: 1rem;
  }
}

/*begin hamburger*/

#nav-icon1 {
  width: 1.3rem;
  height: 16px;
  position: relative;
  transform: rotate(0deg);
  transition: 0.4s ease-in-out;
  cursor: pointer;
}

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #ff4000;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

#nav-icon1 span {
  &:nth-child(1) {
    top: 0;
  }
  &:nth-child(2) {
    top: 8px;
    display: none;
  }
  &:nth-child(3) {
    top: 12px;
  }
}

.navbar-toggler[aria-expanded="true"] #nav-icon1 {
  max-width: 27px;
  span:nth-child(1) {
    top: 5px;
    transform: rotate(135deg);
  }
  span:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  span:nth-child(3) {
    top: 5px;
    transform: rotate(-135deg);
  }
}

#touch-menu {
  z-index: 10;
}

/*end hamburger*/
// section dark
.section-dark {
  padding: 5rem 0;
  background-color: #141414;
  .content-box {
    max-width: 64.79%;
  }
}
.content-box {
  padding: 2rem 2rem 2rem $pdgleft-section;
  background-color: #fff;
  color: #575757;
  font-weight: 300;
}
.content__title {
  position: relative;
  padding-left: 2rem;
  font-family: $font-asap;
  font-size: 1.6rem;
  text-transform: uppercase;
  font-stretch: condensed;
  line-height: 1;
  letter-spacing: 0.175em;
  color: #ff4000;
  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    display: inline-block;
    width: 1rem;
    height: 1rem;
    background-color: #ff4000;
  }
}
.content__text {
  padding-left: 2rem;
  ul {
    padding-left: 0;
  }
}
.new-york {
  margin-top: -3rem;
  opacity: 0.5;
  width: 100%;
}
.new-york2 {
  position: relative;
  background-color: #141414;
  .new-york2 {
    max-width: 90%;
  }
  .orange-label {
    position: absolute;
    right: 0;
    top: 0;
    width: 43.75vw;
    padding: 2rem;
    font-family: $font-shareTech;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    text-align: center;
    color: #ffffff;
    background-color: #ff4000;
  }
}
.section-content {
  position: relative;
}
.herix-re {
  width: 36rem;
  max-width: 55.25vw;
  padding: 3.4rem 2.9rem 3rem 2.9rem;
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 1;
}
// Footer
.footer {
  display: flex;
  color: #fff;
  &__cover {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 2rem 0 1rem;
    flex-grow: 1;
    padding-left: $pdgleft;
    background-size: cover;
    font-size: 0.5rem;
    .navbar-nav {
      margin-top: 1.7rem;
    }
  }
  &__logo {
    width: 2rem;
  }
  .nav-link {
    font-size: 14px;
    font-family: $font-shareTech;
    letter-spacing: 0.1em;

    &:hover {
      color: #ff4000;
    }
  }
  &__copy {
    font-size: 14px;
    margin: 1.7rem 0 0 0;
    letter-spacing: 0.01em;
  }
  &__menu {
    min-width: 25vw;
    padding: 2rem 2.7rem 2rem;
    .nav-link {
      color: #575757;
      font-size: 16px;
      text-decoration: underline;
    }
  }
}
